import React from "react";

export default function Spinner() {
  return (
    <div>
      <span
        className="loader"
        style={{
          display: "block",
          zIndex: "1000",
          position: "absolute",
          left: window.innerWidth / 2 - 30 + "px",
          top: "6px",
        }}
      ></span>
    </div>
  );
}
